.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Login form styles */
.login-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #f5f7fb;
}
.login-form {
  background: white;
  padding: 24px;
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0,0,0,0.08);
  width: 320px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  color: #222;
}
.login-form h2 {
  margin: 0 0 8px 0;
  text-align: center;
}
.login-form label {
  display: flex;
  flex-direction: column;
  font-size: 14px;
}
.login-form input {
  padding: 8px 10px;
  margin-top: 6px;
  border: 1px solid #d1d7e0;
  border-radius: 4px;
}
.login-button {
  padding: 10px 12px;
  background: #1976d2;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 600;
}
.login-button:hover { background: #155fa0; }
.login-error { color: #b00020; font-size: 13px; text-align: center; }

/* Academic calendar table */
.calendar-wrap {
  padding: 28px;
  min-height: 100vh;
  background: #fafbfd;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.calendar-table {
  border-collapse: collapse;
  width: 100%;
  max-width: none;
  margin-top: 12px;
  font-size: 15px;
}
.calendar-table th,
.calendar-table td {
  border: 1px solid #d8dbe0;
  padding: 10px 12px;
  text-align: left;
}
.calendar-table thead th {
  background: #fff2cc;
  color: #7a1a00;
  font-size: 15px;
}
.calendar-actions { width: 100%; max-width: none; display:flex; justify-content:flex-end; }
.small-btn { background:#1976d2; color:white; border:none; padding:5px 8px; border-radius:4px; cursor:pointer; font-size:13px }
.calendar-note { margin-top:10px; color:#444; max-width:none; width:100%; font-size:14px }

/* reduce input sizes in calendar */
.calendar-table input[type="date"], .batch-name-input {
  padding: 6px 8px;
  font-size: 15px;
}
.batch-card { background: white; border-radius:6px; padding:12px; box-shadow: 0 6px 16px rgba(0,0,0,0.04); }
.batch-name-input { width:100%; padding:6px 8px; border:none; background:transparent; color:#fff; font-weight:800; font-size:16px }

.batch-header { display:flex; align-items:center; gap:8px; justify-content:space-between }
.remove-btn { background: transparent; color: #d32f2f; border: none; padding:4px; cursor:pointer; font-size:18px; line-height:1 }
.remove-btn:hover { opacity:0.8 }
.batch-name-input { color:#000; font-weight:900; font-size:16px }

/* batches grid */
.batches-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
  width: 100%;
  max-width: none;
  margin: 4px 0 0 0;
}
.batch-card { background: white; border-radius:6px; padding: 6px; box-shadow: 0 6px 16px rgba(0,0,0,0.04); }
.batch-name-cell { background:#1976d2; color:white; text-align:center; }
.batch-name-input { width:100%; padding:8px 10px; border:none; background:transparent; color:white; font-weight:700; font-size:16px }

@media(min-width:900px) {
  /* Keep single-column stacking even on desktop per request */
  .batches-grid { grid-template-columns: 1fr; gap: 12px; }
}

/* allow grid items to shrink instead of forcing overflow */
.batch-card { min-width: 0; }

/* ensure inputs don't force column width */
.calendar-table input[type="date"], .calendar-table input[type="text"] {
  max-width: 100%;
  box-sizing: border-box;
}

/* ensure table columns can shrink and wrap content */
.calendar-table { table-layout: fixed; }
.calendar-table td, .calendar-table th { overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

/* New visual theme for calendar tables */
.batch-card { border-left: 6px solid rgba(25,118,210,0.12); }
.calendar-table thead th {
  background: linear-gradient(90deg, #1e88e5 0%, #1565c0 100%);
  color: #ffffff;
  font-weight: 700;
  letter-spacing: 0.2px;
}
.calendar-table tbody tr:nth-child(even) { background: #fbfdff; }
.calendar-table tbody tr:hover { background: #eef7ff; }
.calendar-table td, .calendar-table th { border-color: #e3edf9; }
.batch-name-cell { background: linear-gradient(90deg,#1976d2 0%, #1565c0 100%); }
.batch-card { box-shadow: 0 8px 18px rgba(21, 101, 192, 0.06); }
.batch-header { padding: 4px 2px; }
.batch-name-input { background: transparent; border: none; color: #fff; font-weight: 800; }
.remove-btn { color: #fff; background: rgba(255,255,255,0.14); border-radius:4px; padding:6px; }
.remove-btn:hover { background: rgba(255,255,255,0.22); }

/* subtle divider between the two batch cards */
.batches-grid { position: relative; }
.batches-grid::before { display: none; }

/* Search box */
.calendar-search { width: 100%; max-width: 900px; margin: 12px auto 0 auto; display:flex; justify-content:flex-start }
.calendar-search { width: 100%; max-width: 900px; margin: 18px auto 6px auto; display:flex; justify-content:center }
.calendar-search input { width: 420px; max-width:92%; padding:12px 18px 12px 44px; border-radius:999px; border:none; font-size:15px; box-shadow: 0 6px 18px rgba(21,101,192,0.08); background-color:#fff; background-repeat:no-repeat; background-position:14px center; background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='18' height='18' viewBox='0 0 24 24'><path fill='%231565c0' d='M21 20l-5.6-5.6A7 7 0 1 0 17 17.4L22 22zM10 16a6 6 0 1 1 0-12 6 6 0 0 1 0 12z'/></svg>"); transition: box-shadow .15s ease, transform .08s ease }
.calendar-search input:focus { outline:none; box-shadow: 0 10px 28px rgba(21,101,192,0.12); transform: translateY(-1px); }
.calendar-search input::placeholder { color:#98a3b8 }

/* modal styles */
.modal-overlay { position:fixed; inset:0; background:rgba(0,0,0,0.35); display:flex; align-items:center; justify-content:center; z-index:60 }
.modal { background:white; padding:20px; border-radius:10px; width:320px; box-shadow:0 18px 60px rgba(33,66,99,0.25) }
.modal input { padding:8px 10px; border:1px solid #d1d7e0; border-radius:6px }

.modal select { padding:8px 10px; border:1px solid #d1d7e0; border-radius:6px }
.modal ul { margin:8px 0 0 18px }
.batch-card ul { margin:8px 0 0 18px }
.no-results { margin: 12px 0; color:#666; font-size:14px }

/* make tables use available width inside cards */
.batch-card .calendar-table { width: 100%; }
.batch-card { align-self: stretch; }


